import SimpleBBCodeDataProcessor from './simplebbcodedataprocessor';
// Simple plugin which loads the data processor.
function BBCode( editor ) {
	editor.data.processor = new SimpleBBCodeDataProcessor();
}

// To override the default styles, this file must be imported after ClassicEditor.
import './custom.css';


export default class ClassicEditor extends ClassicEditorBase {}

// Plugins to include in the build.
ClassicEditor.builtinPlugins = [
	BBCode,

	Essentials,
	Autoformat,

	Bold,
	Italic,
	Underline,
	Strikethrough,
	Superscript,
	Subscript,
	BlockQuote,
	Heading,
	Link,
	Paragraph,

	Alignment,
	Font,

	MediaEmbed,

	CKFinder,
];

// Editor configuration.
ClassicEditor.defaultConfig = {
	toolbar: {
		items: [
			'heading',
			'|', 'bold', 'italic', 'underline', 'strikethrough',
			'|', 'fontColor', 'alignment',
			'|', 'subscript', 'superscript', 'blockQuote',
			'|', 'link',
			'|', 'undo', 'redo'
		]
	},
	// This value must be kept in sync with the language defined in webpack.config.js.
	language: 'en'
};
